.referrer {
  grid-area: referrer;

  &__table {
    border-spacing: 0px;
    border-radius: 4px;
    border: 1px solid #e6eaee;
    background-color: #ffffff;
    margin-left: 30px;
    padding: 28px 30px 0 30px;
    width: 770px; }

  &__table-header {
    text-align: left; }

  &__table-name {
    font-size: 24px;
    color: #354052; }

  &__table-header-column-name {
    font-family: "Source Sans Pro Semibold";
    font-size: 14px;
    color: #7f8fa4; }

  &__table-body {
    font-family: "Source Sans Pro Semibold";
    font-size: 14px;
    color: #333c48; }

  &__table-row {
    line-height: 35px; }

  &__table-row:nth-child(even) {
    background-color: #f5f8fa; } }
